.login .form {
    display: flex;
    flex-direction: column;
}
.login .form input {
    margin: 8px 0;
    width: 30vw;
    padding: 12px;
    border-radius: 10px;
    border: none;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    letter-spacing: 2px;
    color: #000;
}
.login .form input::placeholder{
    font-weight: bold;
    color: #000;
}
