.footer {
    background-color: #333;
    padding: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer ul li {
    margin: 10px;
    list-style-type: none;
}
.footer ul li button {
    background-color: transparent;
    padding: 10px;
    color: whitesmoke;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    letter-spacing: 1px;
    transition: background-color 0.4s ease-in-out;
}
.footer ul li button:hover{
    background-color: whitesmoke;
}
