.destination .grid-items  {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    padding: 40px;
    
}

.destination h1 {
    text-align: center;
    font-size: 30px;
    color: green;
}
.destination .grid-items div{
    background-color: #f1f1f1;
    /* margin: 0px 20px; */
    padding: 20px;
    border-radius: 10px;
}
.destination .grid-items img {
    border-radius: 10px;
}

.destination .grid-items div h4{
    padding: 10px 0;
    font-size: 22px;
}
.destination .grid-items div p {
    line-height: 1.8;
    /* font-weight: bold; */
}
.destination img {
    width: 100%;
    
}
/* .destination p{
    color: red;
} */

@media only screen and (max-width: 768px) {
    .destination .grid-items {
      display: grid;
      grid-template-columns: 1fr;
      gap: 30px;
      
    }

.grid-items h4 {
    text-align: center;
}  
}