.showcase{
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url("../asset/pplane.jpg");
}
.showcase .overlay {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.6);
}
.showcase h1 {
    font-size: 36px;
    padding-bottom: 20px;
    background-color: whitesmoke;
    color: #000;
    padding: 10px;
    border-radius: 10px;
}
@media only screen and (max-width: 768px) {
    .showcase .overlay h1 {
        font-size: 40px;
        text-align: center;
    }
    .showcase .overlay p {
        font-size: 22px;
    }
}