.login .form button {
    padding: 10px;
    border: 2px solid #1e427c;
    background-color: #1e427c;
    color: #fff;
    margin-top: 8px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    border-radius: 10px;
    transition: background-color 3s ease-in-out, border 3s;
}
.login .form button:hover {
    background-color: transparent;
    border: none;
    cursor: pointer;
}